@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
    .header-button{
        @apply border-solid bg-white text-black rounded-xl p-1 font-semibold w-24
               hover:shadow-gray-500 hover:shadow-lg transition-all duration-300 hover:text-orange-400;
    }

    .login-bg{
        @apply bg-gray-600 h-screen w-screen flex items-center justify-center;
    }
    .login-island{
        @apply bg-gray-800 flex w-[40rem] h-[26em] relative justify-center p-5 shadow-black shadow-md rounded-lg;
    }
    .register-island{
        @apply bg-gray-800 flex w-[40rem] h-[30em] relative justify-center p-5 shadow-black shadow-md rounded-lg;
    }
    .login-input{
        @apply  w-full h-8 border-b-4 bg-gray-900 pt-5 pb-5 pl-2 pr-2 text-orange-600 border-none rounded-md
                focus:outline-none transition-all duration-1000 focus:placeholder:text-orange-800
                placeholder:text-zinc-500;
    }
    .login-text{
        @apply text-left text-zinc-500 font-bold;
    }
    .login-button{
        @apply bg-orange-700 rounded-md h-10 font-semibold transition-all duration-200 
               hover:bg-orange-500;
    }
    .register-button{
        @apply bg-orange-700 rounded-md h-10 font-semibold transition-all duration-200 absolute bottom-[1vh] right-0 left-0 w-[90%] ml-auto mr-auto mb-2
               hover:bg-orange-500;
    }
    .login-error-message{
        @apply text-2xl text-red-700;
    }

    .sidebar{
        @apply relative w-20 top-0 left-0 h-screen flex flex-col bg-gray-900 text-white shadow-lg;
    }
    .sidebar-icon{
        @apply relative flex items-center justify-center h-16 w-16 mt-2 mb-2 mx-auto bg-gray-800 text-orange-600 rounded-3xl
               cursor-pointer hover:bg-orange-600 hover:text-white transition-all duration-200 hover:rounded-sm;
    }
    .sidebar-tooltip{
        @apply absolute w-auto p-2 m-2 min-w-max left-14 rounded-md shadow-md text-white bg-gray-900 text-xs font-bold
               transition-all duration-100 scale-0 origin-left;
    }
    .red-dot{
        @apply absolute bg-red-600 w-5 h-5 rounded-full right-0 bottom-0;
    }

    .friend-container{
        @apply h-8 w-auto p-3 text-gray-400 text-left flex items-center text-xl m-2 rounded-sm
               hover:bg-gray-600;
    }
    .add-friend-button{
        @apply h-8 bg-green-600 text-white m-4 justify-center items-center flex rounded-md
               hover:bg-green-400 transition-all duration-150;
    }
    .message_container_chat{
        @apply border-t-2 border-gray-900 flex flex-col flex-grow min-h-0;
    }
    .chat-elem{
        @apply flex text-gray-400 h-10 p-2 text-lg w-auto items-center
               hover:bg-gray-600;
    }

    .friend-request-elem-buttons{
        @apply border-none cursor-pointer text-xl font-normal w-24 rounded-md ml-3 text-white p-1;
    }
    
    .friend_header_container{
        @apply p-4 text-[#d9e0eb] border-b-2 border-gray-900;
    }
}

@layer utilities {
    .scrollbar::-webkit-scrollbar {
        width: 7px;
        height: 10px;
    }
    .scrollbar::-webkit-scrollbar-track {
        border-radius: 100vh;
    }
    
    .scrollbar::-webkit-scrollbar-thumb {
        background: #202225;
        border-radius: 100vh;
        border: 3px solid #202225;
        display: solid;
    }
    
    .scrollbar::-webkit-scrollbar-thumb:hover {
        display: solid;
    }
}

