/*      Header Page          */
/* ||||||||||||||||||||||||| */

.header
{
    height: 3em;
    padding: 1em 14.125em;
    background-color: #1e2124;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    font-family: Arial, Helvetica, sans-serif;
    color: white;
}

.header > *
{
    padding: 0px 10px;
}

