{
    color: #1e2124;
    font-family: Arial, Helvetica, sans-serif;
}

#header-login-button
{
    margin-left: auto;
}

@media screen and (max-width: 750px) {
    .header  button
    {
        position: relative;
        background-color: white ;
        border: none;
        border-radius: 10px;
        width: 5em;
        height: 3.125em;
        font-size: 16px;
    }

    #header-login-button
    {
        margin-left: auto;
        margin-right: 1rem;
    }

    .header 
    {
        padding: 1rem;
    }
}

